<template>

    <section class="section">

        <button class="button is-primary is-pulled-right" @click="addFormIsVisible = true">Add Feature</button>

        <h1 class="title">Feature List</h1>

        <h2 class="subtitle">{{ client.name }}</h2>

        <Feature v-for="feature in client.feature_flags" :feature="feature" :clientId="clientId"></Feature>

        <modal v-show="addFormIsVisible" @close="addFormIsVisible = false" @ok="addFeature">

            <template v-slot:title>Add a Feature</template>

            <div class="field">
                <label class="label">Name *</label>
                <div class="control">
                    <input class="input" type="text" placeholder="Feature Name" v-model="newFeature.description" required>
                </div>
            </div>

            <div class="field">
                <label class="label">Machine Name *</label>
                <div class="control">
                    <input class="input" type="text" placeholder="feature_name" v-model="newFeature.name" required>
                </div>
            </div>

        </modal>

    </section>

</template>

<script>
    import FeaturesAPI from './../classes/featuresapi.js';
    import Feature from './../components/Feature.vue';
    import modal from './../components/Modal.vue';

    export default {
        name: 'Features',

        components: { Feature, modal },

        data() {
            return {
                clientId: null,
                client: {},
                addFormIsVisible: false,
                newFeature: {name: '', description: ''}
            }
        },

        methods: {
            addFeature() {
                let self = this;

                FeaturesAPI.post(
                    'flag/'+self.clientId,
                    {
                        name: self.newFeature.name,
                        description: self.newFeature.description
                    },
                    (response) => {
                        self.client.feature_flags.push(response.data.data);
                        self.newFeature.name = '';
                        self.newFeature.description = '';
                        self.addFormIsVisible = false;
                    }
                );
            }
        },

        mounted() {
            this.clientId = this.$route.params.pathMatch;

            FeaturesAPI.get(
                'client/'+this.clientId,
                (response) => this.client = response.data.data
            );
        }
    }

</script>