import VueRouter from 'vue-router';

let routes = [
    {
        path: '/',

        component: require('./views/Clients.vue').default,

        meta: { requiresAuth: true }
    },

    {
        path: '/client/*',

        component: require('./views/Features.vue').default,

        meta: { requiresAuth: true }
    },

    {
        path: '/sdks',

        component: require('./views/SDKs.vue').default,

        meta: { requiresAuth: true }
    },

    {
        path: '/login*',

        component: require('./views/Login.vue').default
    },

    {
        path: '/logout',

        component: require('./views/Logout.vue').default
    },

    {
        path: '/users',

        component: require('./views/Users.vue').default,

        meta: { requiresAuth: true }
    },

    {
        path: '/logs/*',

        component: require('./views/Logs.vue').default,

        meta: { requiresAuth: true }
    }
];

let router = new VueRouter({routes, linkActiveClass: 'is-active'});

router.beforeResolve((to, from, next) => {
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (!sessionStorage.getItem('auth-token')) {
            next({
                path: '/login'
            });
        }
    }

    next();
});

export default router;
