<template>

    <article :class="['message', featureStyle]">

        <div class="message-header">
            <p>{{ feature.description }}</p>
            <span class="is-pulled-right">
                <span class="feature-machine-name">{{ feature.name }}</span> |
                <router-link :to="'/logs/' + feature.id" exact>
                    <a>Logs</a>
                </router-link>
            </span>
        </div>

        <div class="message-body">
            <div class="tabs">
                <ul>
                    <li :class="{ 'is-active': (environment == 'development') }"><a @click.prevent="environment = 'development'">Development</a></li>
                    <li :class="{ 'is-active': (environment == 'staging') }"><a @click.prevent="environment = 'staging'">Staging</a></li>
                    <li :class="{ 'is-active': (environment == 'production') }"><a @click.prevent="environment = 'production'">Production</a></li>
                </ul>
            </div>


            <flag v-show="environment == 'development'" :clientId="clientId" :environment="'development'" :flag="getFlag('development')" :flagName="feature.name"></flag>

            <flag v-show="environment == 'staging'" :clientId="clientId" :environment="'staging'" :flag="getFlag('staging')" :flagName="feature.name"></flag>

            <flag v-show="environment == 'production'" :clientId="clientId" :environment="'production'" :flag="getFlag('production')" :flagName="feature.name"></flag>

        </div>

    </article>

</template>

<script>
import Flag from './../components/Flag.vue';

export default {
    name: 'Feature',

    props: [ 'feature', 'clientId' ],

    components: { Flag },

    data() {
        return {
            environment: 'development'
        }
    },

    computed: {
        featureStyle() {
            switch (this.environment) {
                case 'development':
                    return 'is-primary';
                case 'staging':
                    return 'is-warning';
                default:
                    return 'is-danger';
            }
        }
    },

    methods: {
        getFlag(environment) {
            let env_state = null;

            this.feature.enabled_states.forEach((state) => {
                if (state.environment === environment) {
                    env_state = state;
                }
            });

            return env_state;
        }
    }
}
</script>

<style scoped>

    .feature-machine-name {
        font-weight: lighter;
        color: hsl(0, 0%, 90%);
    }

</style>
