<template>

    <div>

        <section class="hero is-primary is-small" v-show="loggedIn">
            <div class="hero-head">
                <nav class="navbar">
                    <div class="container">
                        <div class="navbar-brand">
                            <a class="navbar-item">
                                <img src="./../src/assets/propeller.png" alt="Logo">
                            </a>
                            <span class="navbar-burger burger" data-target="navbarMenuHeroA">
                            <span></span>
                            <span></span>
                            <span></span>
                          </span>
                        </div>
                        <div id="navbarMenuHeroA" class="navbar-menu">
                            <div class="navbar-end">
                                <router-link to="/logout">Logout</router-link>
                            </div>
                        </div>
                    </div>
                </nav>
            </div>

            <!-- Hero content: will be in the middle -->
            <div class="hero-body">
                <div class="container has-text-centered">
                    <img class="primary-logo" src="./../src/assets/propeller.png" alt="Logo">
                    <h2 class="subtitle">
                        Feature Manager
                    </h2>
                </div>
            </div>

            <!-- Hero footer: will stick at the bottom -->
            <div class="hero-foot">
                <nav class="tabs is-boxed">
                    <div class="container">
                        <ul>
                            <router-link tag="li" to="/" exact>
                                <a>Clients</a>
                            </router-link>

                            <router-link tag="li" to="/users">
                                <a>Users</a>
                            </router-link>

                            <router-link tag="li" to="/docs">
                                <a>Documentation</a>
                            </router-link>

                            <router-link tag="li" to="/sdks">
                                <a>SDKs</a>
                            </router-link>
                        </ul>
                    </div>
                </nav>
            </div>
        </section>

        <section class="section">

            <div class="container">

                <router-view></router-view>

            </div>

        </section>

    </div>

</template>

<script>
export default {

    name: 'app',

    data() {
        return {
            loggedIn: false
        }
    },

    created() {
        if (sessionStorage.getItem('auth-token')) {
            this.loggedIn = true;
        }

        let self = this;

        window.addEventListener('auth-error', (event) => {
            self.loggedIn = false;
            self.$router.push('/login/failed');
        });

        window.addEventListener('login', (event) => {
            self.loggedIn = true;
        });

        window.addEventListener('logout', (event) => {
            self.loggedIn = false;
        });
    }
}
</script>

<style>

    .primary-logo {
        width: 200px;
    }

    .list {
        margin-left: 0 !important;
    }

</style>
