<template>

    <div id="login">
        <div class="login-card">

            <div class="card-title">
                <h1>Please Sign In</h1>
            </div>

            <div class="content">
                <form method="POST" action="" @submit.prevent="login">

                    <input id="email" type="email" name="email" title="email" placeholder="Email" v-model="email_address" required autofocus>
                    <input id="password" type="password" name="password" title="password" v-model="password" placeholder="Password" required>

                    <p class="help is-danger" v-show="loginFailed">
                        Invalid email address or password
                    </p>

                    <div class="level options">
                        <div class="checkbox level-left">
                            <input type="checkbox" id="checkbox" class="regular-checkbox">
                            <label for="checkbox"></label>
                            <span>Remember me</span>
                        </div>

                        <a class="btn btn-link level-right" href="#">Forgot Password?</a>
                    </div>

                    <button type="submit" class="btn btn-primary">Login</button>
                </form>
            </div>
        </div>
    </div>

</template>

<script>
    import FeaturesAPI from './../classes/featuresapi.js';

    export default {

        name: 'Login',

        data() {
            return {
                'email_address': '',
                'password': ''
            }
        },

        computed: {
            loginFailed() {
                console.log(this.$route.params.pathMatch);
                return (this.$route.params.pathMatch === '/failed');
            }
        },

        methods: {
            login() {
                let self = this;

                FeaturesAPI.post(
                    'login',
                    this.$data,
                    (response) => {
                        let token = response.data.data.auth_token;
                        sessionStorage.setItem('auth-token', token);
                        FeaturesAPI.setAuthToken(token);
                        window.dispatchEvent(new CustomEvent('login'));
                        self.$router.push('/');
                    }
                );
            }
        }

    };

</script>

