<template>
    <section class="section">

        <h1 class="title">Logs for {{ featureFlag.description }}</h1>

        <div class="content">

            <table>
                <tr>
                    <th>Email</th>
                    <th>Type</th>
                    <th>Environment</th>
                    <th>Information</th>
                    <th>Occurred</th>
                </tr>
                <tr v-for="log in logs">
                    <td>{{ log.email }}</td>
                    <td>{{ log.type }}</td>
                    <td>{{ log.environment }}</td>
                    <td>{{ log.information }}</td>
                    <td>{{ logOccuredDisplay(log.occurred) }}</td>
                </tr>
            </table>

        </div>

    </section>
</template>

<script>
    import FeaturesAPI from './../classes/featuresapi.js';

    export default {
        name: 'Logs',

        data() {
            return {
                featureFlagId: null,
                featureFlag: {
                    description: null
                },
                logs: []
            }
        },

        methods: {
            logOccuredDisplay(occurred) {
                return new Date(occurred);
            }
        },

        mounted() {
            this.featureFlagId = this.$route.params.pathMatch;

            FeaturesAPI.get(
                'auditlog/flag/' + this.featureFlagId,
                (response) => {
                    this.featureFlag = JSON.parse(response.data.meta.details);
                    this.logs = response.data.data
                }
            );
        }
    }
</script>