<template>
    <section class="section">

        <h1 class="title">Clients</h1>

        <div class="content">

            <div class="list is-hoverable">

                <router-link v-for="client in clients" class="list-item" :to="'/client/'+client.id">{{ client.name }}</router-link>

            </div>

        </div>

    </section>
</template>

<script>
    import FeaturesAPI from './../classes/featuresapi.js';

    export default {
        name: 'Clients',

        data() {
            return {
                clients: []
            }
        },

        mounted() {
            FeaturesAPI.get(
                'client',
                (response) => this.clients = response.data.data
            );
        }
    }
</script>