<template>

    <article class="media">
        <div class="media-content">

            <div class="content">
                <div class="field">
                    <label class="checkbox">
                        <input type="checkbox" v-model="flag.global_enabled" @change="setFlagState">
                        Enable for Everyone
                    </label>
                </div>
            </div>

            <hr />

            <div class="content">

                <h6 class="subtitle">Emails Enabled</h6>

                <div class="list is-hoverable">
                    <a v-for="email in flag.emails_enabled" class="list-item">
                        {{ email }}
                        <button class="delete  is-pulled-right" @click.prevent="removeEmail(email)"></button>
                    </a>

                    <div class="list-item">
                        <div class="field  is-grouped">
                            <div class="control">
                                <input type="text" v-model="newEmail" class="input" placeholder="Add New Email">
                            </div>
                            <a class="button  is-link" @click="setFlagState">Add</a>
                        </div>
                    </div>
                </div>

            </div>

            <div class="content">

                <h6 class="subtitle">IP Addresses Enabled</h6>

                <div class="list is-hoverable">
                    <a v-for="ip in flag.ips_enabled" class="list-item">
                        {{ ip }}
                        <button class="delete  is-pulled-right" @click.prevent="removeIp(ip)"></button>
                    </a>

                    <div class="list-item">
                        <div class="field  is-grouped">
                            <div class="control">
                                <input type="text" v-model="newIp" class="input" placeholder="Add New IP">
                            </div>
                            <a class="button  is-link" @click="setFlagState">Add</a>
                        </div>
                    </div>
                </div>

            </div>

        </div>

    </article>

</template>

<script>
    import FeaturesAPI from './../classes/featuresapi.js';

    export default {

        name: 'Flag',

        props: ['environment', 'flag', 'clientId', 'flagName'],

        data() {
            return {
                newEmail: '',
                newIp: ''
            }
        },

        methods: {
            getEnabledEmails() {
                if (this.newEmail !== '') {
                    this.flag.emails_enabled.push(this.newEmail);
                    this.newEmail = '';
                }

                return this.flag.emails_enabled;
            },

            getEnabledIPs() {
                if (this.newIp !== '') {
                    this.flag.ips_enabled.push(this.newIp);
                    this.newIp = '';
                }

                return this.flag.ips_enabled;
            },

            removeEmail(email) {
                this.flag.emails_enabled.splice(this.flag.emails_enabled.indexOf(email), 1);

                this.setFlagState();
            },

            removeIp(ip) {
                this.flag.ips_enabled.splice(this.flag.ips_enabled.indexOf(ip), 1);

                this.setFlagState();
            },

            setFlagState() {
                let self = this;

                FeaturesAPI.put(
                    'flag/'+this.clientId+'/'+this.flagName,
                    {
                        environment: self.environment,
                        global_enabled: self.flag.global_enabled,
                        email_addresses: self.getEnabledEmails(),
                        ip_addresses: self.getEnabledIPs()
                    },
                    (response) => {}
                );
            }
        }

    };

</script>

<style scoped>

    .subtitle {
        text-transform: capitalize;
    }

    h6 {
        padding-top: 0.5rem;
        padding-left: 0.5rem;
    }

    .media {
        border-top: none !important;
        margin-top: 0 !important;
        padding-top: 0 !important;
    }

</style>
