<template>

    <section class="section">

        <div class="content">

            <button class="button is-primary is-pulled-right" @click="addFormIsVisible = true">Add User</button>

            <div>
                <h1 class="title">Users</h1>
            </div>

            <ul class="list is-hoverable">

                <a class="list-item" v-for="user in users">

                    {{ user.email_address }}

                </a>

            </ul>

            <modal v-show="addFormIsVisible" @close="addFormIsVisible = false" @ok="createUser()">

                <template v-slot:title>Add a User</template>

                <div class="field">
                    <label class="label">Email Address *</label>
                    <div class="control">
                        <input class="input" type="email" placeholder="name@domain.com" v-model="email" required>
                    </div>
                </div>

                <div class="field">
                    <label class="label">Re-type Email Address *</label>
                    <div class="control">
                        <input class="input" type="email" placeholder="name@domain.com" v-model="emailRetype" required>
                    </div>
                </div>

                <p class="help is-danger" v-show="differentEmails">
                    Email addresses do not match
                </p>

                <div class="field">
                    <label class="label">Password *</label>
                    <div class="control">
                        <input class="input" type="password" v-model="password" required>
                    </div>
                </div>

                <div class="field">
                    <label class="label">Re-type Password *</label>
                    <div class="control">
                        <input class="input" type="password" v-model="passwordRetype" required>
                    </div>
                </div>

                <p class="help is-danger" v-show="differentPasswords">
                    Passwords do not match
                </p>

            </modal>

        </div>

    </section>

</template>

<script>
    import modal from './../components/Modal.vue';
    import FeaturesAPI from './../classes/featuresapi';

    export default {

        name: 'Users',

        components: { modal },

        data() {
            return {
                addFormIsVisible: false,

                users: [],

                email: '',
                emailRetype: '',
                password: '',
                passwordRetype: '',
                differentEmails: false,
                differentPasswords: false
            }
        },

        methods: {
            clearErrors() {
                this.differentEmails = false;
                this.differentPasswords = false;
            },

            resetForm() {
                this.email = '';
                this.emailRetype = '';
                this.password = '';
                this.passwordRetype = '';
            },

            createUser() {
                this.clearErrors();

                let valid = true;

                if (this.email !== this.emailRetype) {
                    this.differentEmails = true;
                    valid = false;
                }

                if (this.password !== this.passwordRetype) {
                    this.differentPasswords = true;
                    valid = false;
                }

                if (valid) {
                    let self = this;

                    FeaturesAPI.post(
                        'user',
                        {
                            email_address: this.email,
                            password: this.password
                        },
                        (response) => {
                            self.users.push({ email_address: self.email });
                            self.resetForm();
                            self.addFormIsVisible = false;
                        }
                    );
                }
            }
        },

        created() {
            FeaturesAPI.get(
                'user',
                (response) => this.users = response.data.data
            );
        }

    }

</script>
